* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: linear-gradient(to bottom, hsl(273deg, 75%, 66%) 0%, hsl(240deg, 73%, 65%) 100%);
  font-family: "Kumbh Sans", sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
}

.wrapper {
  position: relative;
  grid-gap: 20px;
  background-color: hsl(0deg, 0%, 100%);
  background-image: url(../img/bg-pattern-mobile.svg);
  background-repeat: no-repeat;
  background-position: 43% 0;
  background-size: 250px;
  border-radius: 30px;
  grid-template-rows: 1fr 4fr;
  padding: 0 30px 40px 30px;
  width: 90%;
}
@media (min-width: 768px) {
  .wrapper {
    background-image: url(../img/bg-pattern-desktop.svg);
    background-position: -387px -198px;
    background-size: 761px;
    display: grid;
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
    max-width: 880px;
    padding: 0;
  }
}
.wrapper__image--inner {
  background-image: url(../img/illustration-woman-online-desktop.svg);
  background-position: -78px 62px;
  background-repeat: no-repeat;
  background-size: 460px;
  height: 100%;
}

.box {
  display: none;
}
@media (min-width: 768px) {
  .box {
    display: block;
    bottom: 130px;
    left: -95px;
    position: absolute;
  }
}

.box2 {
  position: absolute;
  bottom: 85%;
  left: 15%;
  width: 230px;
}
@media (min-width: 768px) {
  .box2 {
    display: none;
  }
}

@media (min-width: 768px) {
  .accordion {
    padding: 55px 70px 60px 0;
  }
}
.accordion__title {
  font-size: 2em;
  text-align: center;
  margin-block: 130px 20px;
}
@media (min-width: 768px) {
  .accordion__title {
    text-align: left;
    margin: 0;
  }
}
.accordion__questions {
  padding-block: 20px;
  border-bottom: 1px solid, rgba(120, 120, 135, 0.1);
}
.accordion__radio {
  display: none;
}
.accordion__question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  color: hsl(240deg, 6%, 50%);
}
.accordion__question:hover {
  color: hsl(14deg, 88%, 65%);
  cursor: pointer;
}
.accordion__arrow {
  margin-right: 10px;
  transition: all 0.3s ease-out;
}
.accordion__answer {
  font-size: 12px;
  color: hsl(240deg, 6%, 50%);
  height: 0;
  overflow: hidden;
  padding-right: 21px;
  transition: all 0.3s ease;
}

.accordion__radio:checked + .accordion__question {
  font-weight: 700;
  color: hsl(238deg, 29%, 16%);
}
.accordion__radio:checked + .accordion__question > .accordion__arrow {
  transform: rotate(180deg);
}
.accordion__radio:checked ~ .accordion__answer {
  height: 45px;
  margin-top: 10px;
}